}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/../fonts/opensanssemibold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
  scrollbar-color: var(--color-gray-500) transparent;
  scrollbar-width: thin;
}
html::-webkit-scrollbar {
  width: 10px;
}
html::-webkit-scrollbar-track {
  -webkit-box-shadow: none;
  background-color: transparent;
  border-radius: 10px;
}
html::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: var(--color-gray-500);
}

:focus {
  outline: none;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Nunito", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: var(--color-gray-900);
  max-width: 100%;
  overflow-x: hidden;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

p {
  font-size: 16px;
  line-height: 22px;
  margin-top: 0;
}
p:not(:last-child) {
  margin-bottom: 15px;
}

pre {
  padding: 15px;
  width: max-content;
}

.site-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 100vh;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.is-hidden {
  display: none !important;
}

.wpcf7 form .wpcf7-response-output {
  margin: 0;
  border-radius: 4px;
  border-width: 1px;
  padding: 10px 15px;
  text-align: center;
  font-weight: 600;
  font-size: 14px;
}

.wpcf7-spinner {
  position: absolute;
  top: -10px;
  left: -10px;
  bottom: -10px;
  right: -10px;
  width: auto;
  height: auto;
  background-color: rgba(22, 29, 43, 0.05);
  opacity: 1;
  border-radius: 4px;
  margin: 0;
}
.wpcf7-spinner::before {
  width: 30px;
  height: 30px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--corporate-v-2);
}

.wpcf7-response-output {
  color: var(--color-status-danger);
  border: none;
  padding: 0;
  font-weight: bold;
  border-radius: 0;
  text-align: left;
  width: auto;
  max-width: max-content;
}

.wpcf7 form.invalid .wpcf7-response-output {
  border-color: var(--color-status-danger);
}

.wpcf7 form.sent .wpcf7-response-output {
  color: var(--color-status-succes);
}

.wpcf7-form-control-wrap {
  display: flex;
  flex-direction: column;
}

.wpcf7-list-item {
  font-size: 14px;
  margin: 0;
}
@media screen and (max-width: 767px) {
  .wpcf7-list-item {
    margin-top: 10px;
  }
}

@media screen and (min-width: 768px) {
  .wpcf7-submit {
    margin-right: 15px;
  }
}

.wpcf7-list-item-label {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 16px;
  max-width: 1440px;
  width: 100%;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.text__block ul {
  margin: 0 0 25px 0;
  padding: 0 0 0 20px;
  list-style: none;
}
.text__block ul li {
  padding-left: 15px;
  position: relative;
  font-size: 16px;
  line-height: 22px;
}
.text__block ul li::before {
  content: "";
  width: 5px;
  height: 5px;
  position: absolute;
  left: 0;
  display: inline-block;
  background-color: var(--corporate-v-2);
  border-radius: 50%;
  top: 9px;
}
.text__block ul li:not(:last-child) {
  margin-bottom: 5px;
}
.text__block ol {
  margin: 0 0 25px 20px;
  padding: 0 0 0 15px;
}
.text__block ol li {
  padding-left: 5px;
  position: relative;
  font-size: 16px;
  line-height: 22px;
}
.text__block ol li:not(:last-child) {
  margin-bottom: 5px;
}

.main__indent {
  display: block;
  width: 100%;
  height: 130px;
  position: relative;
  z-index: -1;
}
@media screen and (max-width: 1919px) {
  .main__indent {
    height: 80px;
  }
}

.error_page {
  display: flex;
  justify-content: center;
  width: 100%;
}
.error_page svg {
  width: 688px;
  height: 433px;
}
@media screen and (max-width: 1023px) {
  .error_page svg {
    width: 508px;
    height: 253px;
  }
}
@media screen and (max-width: 767px) {
  .error_page svg {
    width: auto;
    height: auto;
  }
}

.main__button {
  background-color: var(--corporate-v-2);
  border-radius: 4px;
  border: 2px solid var(--corporate-v-2);
  padding: 10px 25px;
  font-size: 16px;
  font-weight: 700;
  color: var(--color-gray-100);
  transition: color 0.2s, background-color 0.2s;
  white-space: nowrap;
}
.main__button.active {
  background-color: var(--color-gray-100);
  color: var(--corporate-v-2);
  transition: color 0.2s, background-color 0.2s;
}
@media (any-hover: hover) {
  .main__button:hover, .main__button:focus {
    background-color: var(--color-gray-100);
    color: var(--corporate-v-2);
    transition: color 0.2s, background-color 0.2s;
  }
}
@media (pointer: coarse) {
  .main__button:focus {
    background-color: var(--color-gray-100);
    color: var(--corporate-v-2);
    transition: color 0.2s, background-color 0.2s;
  }
}
.main__button-outline {
  background-color: var(--color-gray-100);
  color: var(--corporate-v-2);
}
@media (any-hover: hover) {
  .main__button-outline:hover, .main__button-outline:focus {
    background-color: var(--corporate-v-2);
    color: var(--color-gray-100);
    transition: color 0.2s, background-color 0.2s;
  }
}
@media (pointer: coarse) {
  .main__button-outline:focus {
    background-color: var(--corporate-v-2);
    color: var(--color-gray-100);
    transition: color 0.2s, background-color 0.2s;
  }
}
.main__button-outline.active {
  background-color: var(--corporate-v-2);
  color: var(--color-gray-100);
  transition: color 0.2s, background-color 0.2s;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.footnote {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 25px;
  border-top: 1px solid var(--color-gray-200);
  margin-top: 70px;
}
@media screen and (max-width: 767px) {
  .footnote {
    margin-top: 50px;
    flex-direction: column;
    align-items: flex-start;
  }
}
.footnote__content {
  color: var(--corporate-v-0);
  font-size: 16px;
  line-height: 22px;
  padding-right: 20px;
}
@media screen and (max-width: 767px) {
  .footnote__content {
    margin-bottom: 20px;
    padding-right: 0;
  }
}
.footnote__content a {
  color: var(--corporate-v-2);
  font-weight: 600;
  text-decoration: underline;
}
.footnote__title {
  font-weight: 700;
  display: block;
  margin-bottom: 5px;
}

.main__form {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(3, 450px);
  position: relative;
}
.main__form p {
  margin: 0;
}
.main__form .field-file__label--text {
  height: 48px;
  line-height: 48px;
  color: var(--color-gray-600);
  font-size: 14px;
}
@media screen and (max-width: 1023px) {
  .main__form {
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
  }
}
@media screen and (max-width: 767px) {
  .main__form {
    grid-template-columns: 1fr;
  }
}
.main__form-text {
  margin: 0 0 0 25px;
  font-size: 14px;
  color: var(--corporate-v-0);
  line-height: 16px;
}
@media screen and (max-width: 767px) {
  .main__form-text {
    text-align: center;
    margin: 15px 0 0 0;
  }
}
.main__form-link {
  color: var(--corporate-v-2);
  display: contents;
}
.main__form-link:hover, .main__form-link:focus {
  text-decoration: underline;
  color: var(--corporate-v-3);
}
.main__form-field {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.main__form-field--full {
  grid-column-start: 1;
  grid-column-end: 4;
}
@media screen and (max-width: 767px) {
  .main__form-field--full {
    grid-column-end: 1;
  }
}
.main__form-field--buttons {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-top: 10px;
}
@media screen and (max-width: 767px) {
  .main__form-field--buttons {
    flex-direction: column-reverse;
  }
}
.main__form-label {
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
  color: var(--corporate-v-1);
  margin-bottom: 10px;
}
@media screen and (max-width: 1279px) {
  .main__form-label {
    font-size: 14px;
    line-height: 16px;
  }
}
.main__form-input, .main__form-files {
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
  padding: 0 15px;
  height: 48px;
  line-height: 48px;
  color: var(--color-gray-600);
  box-shadow: 0px 2px 6px rgba(52, 73, 94, 0.15);
  text-overflow: ellipsis;
  font-size: 14px;
  position: relative;
  width: 100%;
  overflow: hidden;
  max-width: 500px;
  text-overflow: ellipsis;
}
@media screen and (max-width: 1279px) {
  .main__form-input, .main__form-files {
    max-width: 100%;
  }
}
@media screen and (max-width: 1279px) {
  .main__form-input, .main__form-files {
    font-size: 16px;
  }
}
.main__form-input::-webkit-input-placeholder, .main__form-files::-webkit-input-placeholder {
  color: var(--color-gray-600);
}
.main__form-input:-moz-placeholder, .main__form-files:-moz-placeholder {
  color: var(--color-gray-600);
}
.main__form-input::-moz-placeholder, .main__form-files::-moz-placeholder {
  color: var(--color-gray-600);
}
.main__form-input:-ms-input-placeholder, .main__form-files:-ms-input-placeholder {
  color: var(--color-gray-600);
}
.main__form-input:hover::placeholder, .main__form-files:hover::placeholder {
  opacity: 1;
}
.main__form-input:focus, .main__form-files:focus {
  border: 1px solid var(--color-gray-300);
}
.main__form-input:focus::placeholder, .main__form-files:focus::placeholder {
  opacity: 0.3;
}
.main__form-input--file, .main__form-files--file {
  width: 100%;
}
.main__form-input.wpcf7-not-valid {
  border-color: var(--color-status-danger);
  background-image: url(../img/icon__error.svg);
  background-size: 25px 25px;
  background-repeat: no-repeat;
  background-position: 97% 50%;
}
.main__form-input.wpcf7-not-valid::-webkit-input-placeholder {
  color: var(--color-status-danger);
  opacity: 1;
}
.main__form-input.wpcf7-not-valid:-moz-placeholder {
  color: var(--color-status-danger);
  opacity: 1;
}
.main__form-input.wpcf7-not-valid::-moz-placeholder {
  color: var(--color-status-danger);
  opacity: 1;
}
.main__form-input.wpcf7-not-valid:-ms-input-placeholder {
  color: var(--color-status-danger);
  opacity: 1;
}
.main__form-files {
  padding: 0;
  background-image: url(../img/icon__files.svg);
  background-size: 25px 25px;
  background-repeat: no-repeat;
  background-position: 10px 50%;
}
.main__form-checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.main__form-checkbox::before {
  content: "";
  display: inline-block;
  width: 15px;
  height: 15px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid var(--color-gray-600);
  border-radius: 4px;
  margin-right: 8px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}
.main__form-checkbox + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  text-align: left;
}
@media screen and (max-width: 767px) {
  .main__form-checkbox + label {
    margin-top: 0;
    margin-bottom: 15px;
  }
}
.main__form-checkbox + label::before {
  content: "";
  display: inline-block;
  width: 15px;
  height: 15px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid var(--color-gray-600);
  border-radius: 4px;
  margin-right: 8px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}
.main__form-checkbox:checked + label::before {
  border-color: var(--corporate-v-2);
  background-color: var(--corporate-v-2);
  background-image: url(../img/icon__checked_gray.svg);
  background-repeat: no-repeat;
  background-size: 12px 12px;
}
.main__form-licenze {
  flex-direction: row;
  align-items: center;
  display: flex;
}
@media screen and (max-width: 767px) {
  .main__form-licenze {
    flex-direction: column;
  }
}
.main__form .field-file__input {
  position: absolute;
  opacity: 0;
  top: 0;
  right: 100%;
}
.main__form .field-file__label {
  padding: 0 40px;
  width: 100%;
  height: 100%;
  display: block;
  cursor: pointer;
}
.main__form .field-file__label--text {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  display: block;
}
.main__form-textarea {
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
  padding: 15px;
  height: auto;
  font-size: 14px;
  line-height: 20px;
  min-height: 190px;
  max-height: 190px;
  min-width: 100%;
  max-width: 100%;
  width: 100%;
  color: var(--color-gray-600);
  box-shadow: 0px 2px 6px rgba(52, 73, 94, 0.15);
}
.main__form-textarea::-webkit-input-placeholder {
  color: var(--color-gray-600);
}
.main__form-textarea:-moz-placeholder {
  color: var(--color-gray-600);
}
.main__form-textarea::-moz-placeholder {
  color: var(--color-gray-600);
}
.main__form-textarea:-ms-input-placeholder {
  color: var(--color-gray-600);
}
.main__form-textarea:hover::placeholder {
  opacity: 1;
}
.main__form-textarea:focus {
  border: 1px solid var(--color-gray-300);
}
.main__form-textarea:focus::placeholder {
  opacity: 0.3;
}
.main__form-textarea.wpcf7-not-valid {
  border-color: var(--color-status-danger);
}
.main__form-textarea.wpcf7-not-valid::-webkit-input-placeholder {
  color: var(--color-status-danger);
  opacity: 1;
}
.main__form-textarea.wpcf7-not-valid:-moz-placeholder {
  color: var(--color-status-danger);
  opacity: 1;
}
.main__form-textarea.wpcf7-not-valid::-moz-placeholder {
  color: var(--color-status-danger);
  opacity: 1;
}
.main__form-textarea.wpcf7-not-valid:-ms-input-placeholder {
  color: var(--color-status-danger);
  opacity: 1;
}
.main__form .wpcf7-not-valid-tip {
  font-size: 12px;
  margin: 10px 15px 0;
  font-weight: 600;
  color: var(--color-status-danger);
  width: auto;
  white-space: break-spaces;
}
.main__form p {
  margin: 0;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.tags__item {
  background-color: var(--color-gray-400);
  border-radius: 4px;
  padding: 8px 12px;
  font-size: 12px;
  line-height: 16px;
  color: var(--corporate-v-1);
  margin: 0 5px 5px 0;
  transition: color 0.2s;
}
.tags__item:hover {
  color: var(--color-gray-100);
  transition: color 0.2s;
}

.transition {
  transition: color 0.2s, fill 0.2s, background-color 0.2s;
}

.slaider__list {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  width: 100%;
}
@media screen and (max-width: 1023px) {
  .slaider__list {
    display: none;
  }
}
.slaider__list-item {
  width: 20px;
  height: 5px;
  border-radius: 10px;
  background-color: var(--corporate-v-1);
  cursor: pointer;
}
.slaider__list-item:not(:last-child) {
  margin-right: 5px;
}
.slaider__list-item--active {
  width: 40px;
  background-color: var(--corporate-v-2);
}

.cookies {
  position: fixed;
  bottom: 20px;
  font-size: 14px;
  left: 20px;
  background-color: var(--color-gray-300);
  border-radius: 10px;
  padding: 20px;
  max-width: 300px;
  display: flex;
  flex-wrap: wrap;
  display: none;
  z-index: 100;
}
.cookies button {
  display: table;
  width: auto;
  margin-top: 10px;
}

details {
  position: relative;
  min-height: auto;
  max-height: 100%;
  transition: min-height 1.15s linear, max-height 1.5s linear;
  -webkit-transition: min-height 1.15s linear, max-height 1.5s linear;
  will-change: max-height;
  width: 100%;
}
details summary {
  display: flex;
  cursor: pointer;
  outline: 0;
  align-items: center;
  position: relative;
  /*&::before {
        content: "";
        transition: transform .1s linear;
        -webkit-transition: transform .1s linear;
        background-image: url(../../../../assets/media/arrow.svg);
        background-repeat: no-repeat;
        background-size: 10px 6px;
        margin-left: 15px;
        width: 10px;
        height: 6px;
        order: 3;
    }*/
}
details summary::-webkit-details-marker {
  display: none;
}
details summary::-moz-list-bullet {
  list-style-type: none;
}
details[open] {
  min-height: auto;
  max-height: 100%;
}
details[open] summary ~ * {
  opacity: 1;
}
details[open] summary:before {
  transform: rotate(-180deg);
  -webkit-transform: rotate(-180deg);
  -moz-transform: rotate(-180deg);
}

